import React, { Component, useEffect, useState } from 'react';
// import { Helmet } from "react-helmet";
import DocumentMeta from 'react-document-meta';

import './resource.css';

import PageBanner from '../../components/ui/page-banner/page-banner';
import SectionHeading from '../../components/pages-component/section-heading/section-heading';
import ResourceForm from './components/resource-form/resource-form';
import Modal from '../../components/ui/modal/modal';
import FormSuccessMsg from '../../components/pages-component/form-success-msg/form-success-msg';
import createResource from '../../services/api/create-resource';
import getQueryReport from '../../services/api/get-queries-report'
import * as utilityFunctions from '../../utility-functions/utility-functions';
import { connect } from 'react-redux';
import Header from '../../components/layout/header/header';
import Footer from '../../components/layout/footer/footer';

// import {GetToken} from '../../../../../helpers/token'

const Resource = (props) => {

  // constructor(props) {
  //   super(props);
  //   this.formData = new FormData();
  //   this.isStateRequired = false;
  //   this.allowStateField = false;
  // }

  const [formData, setformData] = useState(new FormData())
  const [isStateRequired, setisStateRequired] = useState(false)
  const [allowStateField, settallowStateField] = useState(false)

  const [title, settitle] = useState('')
  const [link, setlink] = useState('')
  const [description, setdescription] = useState('')
  const [formValid, setformValid] = useState(true)
  const [errorMsg, seterrorMsg] = useState(null)
  const [formSubmissionStart, setformSubmissionStart] = useState(false)
  const [showModal, setshowModal] = useState(false)

  const counter = -1;

  useEffect(() => {
    window.scrollTo(0, 0);
  },[])

  const onClearningFormData = () => {
    formData.delete('title');
    formData.delete('description');
    formData.delete('link');
    formData.delete('file1');
    formData.delete('file2');
    formData.delete('file3');
    formData.delete('file4');
    formData.delete('file5');
  }

  const resetProperties = () => {
    props.natureOfQuery = null;
    props.isStateRequired = false;
    props.allowStateField = false;
  }

  // const onSubmitCallBack = (data) => {
  //   if (data.status === 200) {
  //     props.reset();
  //     console.log(data)
  //   } else if (data.response) {
  //     seterrorMsg(data.response.data._message)
  //     props.showLoader(false)
  //   } else {
  //     seterrorMsg('Something went wrong. Please try again later &#x2639;')
  //     props.showLoader(false)
  //   }
  // }


  const onSubmitCallBack = (data) => {
    console.log(data)
    if (data.status === 200) {
      props.reset();
      console.log(data)
    } if (data.status === 201) {
      document.querySelector(".form--contact-us").reset();
      onClearningFormData();
      resetProperties();
      utilityFunctions.clearSelectField('.select-input');
      setformValid(true)
      seterrorMsg(null)
      setformSubmissionStart(false)
      setshowModal(true)
    } else {
      if (data.response) {
        // console.log(data.response.data);
        setformValid(false)
        seterrorMsg(data.response.data)
        setformSubmissionStart(false)
        // console.log(natureOfQuery);
        seterrorMsg(data.response.data._message)
        props.showLoader(false)
      } else {
        const genericErrorMsg = { Error: ["Oops! Something went wrong, please try again."] };
        setformValid(false)
        seterrorMsg(genericErrorMsg)
        setformSubmissionStart(false)
        props.showLoader(false)
      }
    }
  }

  const onSubmitHandler = (event) => {
    event.preventDefault();
    setformSubmissionStart(true)
    createResource(onSubmitCallBack, formData);
  }

  const validate = () => {
    return title !== '' && link !== '' && description !== '';
  }
  const addFile = () => {
    if (Resource.counter <= 5) {
      Resource.counter = Resource.counter + 1;
      console.log(Resource.counter)
    }
  }

  const onInputChange = (event) => {
    const inputFieldName = event.target.name;
    const targetValue = event.target.value;
    const targetType = event.target.type;

    switch (targetType) {
      case 'checkbox':
        formData.delete('ispublic');
        formData.append('ispublic', targetValue);
        break;

      case 'text':
        if (inputFieldName === 'title') {
          formData.delete('title');
          formData.append('title', targetValue);
        }
        else
          if (inputFieldName === 'link') {
            formData.delete('link');
            formData.append('link', targetValue);
          }
        break;
      case 'textarea':
        formData.delete('Discription');
        formData.append('Discription', targetValue);
        break;

      case 'file':
        if (inputFieldName === 'file1') {
          formData.delete('file1');
          formData.append('file1', event.target.files[0]);
        }
        else
          if (inputFieldName === 'file2') {
            formData.delete('file2');
            formData.append('file2', event.target.files[0]);
          }
          else
            if (inputFieldName === 'file3') {
              formData.delete('file3');
              formData.append('file3', event.target.files[0]);
            } else
              if (inputFieldName === 'file4') {
                formData.delete('file4');
                formData.append('file4', event.target.files[0]);
              } else
                if (inputFieldName === 'file5') {
                  formData.delete('file5');
                  formData.append('file5', event.target.files[0]);
                }
        break;


      default:
        break;
    }

    setformData(formData)
  }

  const closeModal = () => {
    setshowModal(false)
  }

  const queryReport = () => {
    getQueryReport()
  }

  const meta = {
    title: 'Ethereal Machines - Resource',
    meta: {
      property: {
        'title': 'Ethereal Machines - Resource',
        'description': 'Ethereal Machines is glad to assist you with CAD/CAM programming for CNC machining and 3D Printing.',
      }
    }
  };
  return (
    <React.Fragment>
      <Modal show={showModal} clicked={closeModal}>
        <FormSuccessMsg />
      </Modal>
      <div className="page page--contact-us">
        {/* <Helmet>
        <title>Design Help | Ethereal Machines-Bangalore, India.</title>
        <meta name="description" content="Ethereal Machines â€“ get in touch with us."/>
        </Helmet> */}

        <DocumentMeta {...meta} />
        <PageBanner heading={"Ethereal Machines Resource"} subHeading={"Share more..."} classValue={"page-banner--design-help"} />
        <section className="section section--design-help-form">
          <Header />
          <SectionHeading name={"Resource"} classValue={"u-margin-bottom-big u-text-center"} hasSubHeading subHeading={"Share your resource"} />

          <div className="container">
            <ResourceForm
              onInputChange={onInputChange}
              onSubmitHandler={onSubmitHandler}
              formSubmissionStart={formSubmissionStart}
              errorMsg={errorMsg}
              natureOfQuery={props.natureOfQuery}
              isStateRequired={isStateRequired}
              showLoader={formSubmissionStart}
              addFile={addFile}
              counter={Resource.counter} />
          </div>
          <Footer />
        </section>
      </div>
    </React.Fragment>
  );

}

const mapStateToProps = (state) => {
  return {
    session: state.login
  }
}

export default connect(mapStateToProps)(Resource);